html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%; }

svg {
  fill: currentColor; }

[tabindex='-1'] :focus {
  outline: none; }

html {
  font-family: "system-ui", "BlinkMacSystemFont", "-apple-system", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  font-size: 100%; }

@media (max-width: 700px) {
  html {
    font-size: 87.5%; } }

@media (min-width: 1400px) {
  html {
    font-size: 125%; } }

@media (min-width: 1600px) {
  html {
    font-size: 150%; } }

::selection {
  color: #fff;
  background: var(--brand-color); }

p {
  margin-top: 1.5rem;
  margin-bottom: 0;
  line-height: 1.5rem; }

ul,
ol {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem; }
  ul li,
  ol li {
    line-height: 1.5rem; }
  ul ul,
  ul ol,
  ol ul,
  ol ol {
    margin-top: 0;
    margin-bottom: 0; }

blockquote {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  line-height: 1.5rem; }

h1,
h2,
h3,
h4,
h5,
h6,
.heading-one,
.heading-two,
.heading-three,
.heading-four,
.heading-five,
.heading-six {
  margin-bottom: 0;
  font-family: "Constantia", "Lucida Bright", "Lucidabright", "Lucida Serif", "Lucida", "DejaVu Serif", "Bitstream Vera Serif", "Liberation Serif", "Georgia", serif; }

h1,
.heading-one {
  margin-top: 3rem;
  font-size: 5.625rem;
  line-height: 7.5rem; }

h2,
.heading-two {
  margin-top: 3rem;
  font-size: 4.5rem;
  line-height: 6rem; }

h3,
.heading-three {
  margin-top: 3rem;
  font-size: 3.375rem;
  line-height: 4.5rem; }

h4,
.heading-four {
  margin-top: 3rem;
  font-size: 2.25rem;
  line-height: 3rem; }

h5,
.heading-five {
  margin-top: 1.5rem;
  font-size: 1.125rem;
  line-height: 1.5rem; }

h6,
.heading-six {
  margin-top: 1.5rem;
  font-size: 0.75rem;
  line-height: 1.5rem; }

table {
  margin-top: 1.5rem;
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0;
  line-height: 24px; }

hr {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem; }

code,
.text-code {
  vertical-align: bottom; }

.text-lead {
  font-size: 1.125rem;
  line-height: 1.5rem; }

.text-small {
  font-size: 0.875rem; }

.text-leading-none {
  line-height: 1; }

.text-leading-tight {
  line-height: 1.25; }

.text-leading-normal {
  line-height: 1.5; }

.text-leading-loose {
  line-height: 2; }

.text-serif {
  font-family: "Constantia", "Lucida Bright", "Lucidabright", "Lucida Serif", "Lucida", "DejaVu Serif", "Bitstream Vera Serif", "Liberation Serif", "Georgia", serif; }

.text-sans {
  font-family: "system-ui", "BlinkMacSystemFont", "-apple-system", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; }

.text-center {
  text-align: center; }

.text-right {
  text-align: right; }

.text-left {
  text-align: left; }

.text-justify {
  text-align: justify; }

.text-uppercase {
  text-transform: uppercase; }

.text-lowercase {
  text-transform: lowercase; }

.text-normalcase {
  text-transform: none; }

.text-capitalize {
  text-transform: capitalize; }

.text-hug {
  margin-top: 0; }

.text-tracking-tight {
  letter-spacing: -.05em; }

.text-tracking-normal {
  letter-spacing: 0; }

.text-tracking-wide {
  letter-spacing: .05em; }

.text-truncate {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }

.text-whitespace-normal {
  white-space: normal; }

.text-whitespace-pre {
  white-space: pre; }

.text-break-words {
  word-wrap: break-word; }

.text-break-normal {
  word-wrap: normal; }

.text-measure {
  max-width: 30em; }

.text-measure-sm {
  max-width: 20em; }

.text-measure-lg {
  max-width: 34em; }

.visually-hidden,
.skip-link a {
  position: absolute;
  top: auto;
  left: -300%;
  overflow: hidden;
  width: 1px;
  height: 1px; }

.skip-link a:focus {
  position: static;
  width: auto;
  height: auto; }

.appearance-none {
  appearance: none; }

.rotate-90 {
  transform: rotate(90deg); }

.rotate-180 {
  transform: rotate(180deg); }

.rotate-270 {
  transform: rotate(270deg); }
